import React, { FormEvent, useState } from "react";
import {
  TextField,
  FormControlLabel,
  Checkbox,
  Button,
  MenuItem,
  Typography,
} from "@mui/material";
import Divider from "@mui/material/Divider";
import Grid from "@mui/material/Grid2";

const FormularioConsulta = () => {
  const [formData, setFormData] = useState({
    paciente: "",
    fecha: "",
    motivoConsulta: "",
    citaHoy: false,
    diagnostico: "",
    comentarioAdicional: "",
    quienAtendio: "",
    pagoRecibido: 0,
    tipoPago: "",
  });

  const handleChange = (
    event: React.ChangeEvent<
      HTMLInputElement | HTMLTextAreaElement | { name?: string; value: unknown }
    >
  ) => {
    const { name, value, type, checked } = event.target as HTMLInputElement;
    setFormData({
      ...formData,
      [name!]: type === "checkbox" ? checked : value,
    });
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    console.log(formData); // Puedes manejar el envío aquí
  };

  return (
    <div style={{ overflow: "auto", maxHeight: "70vh" }}>
      <Typography variant="h4">Información cita</Typography>
      <Divider sx={{marginY:3}} />
      <form onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid size={{ xs: 12, sm: 6 }}>
            <Grid container spacing={3}>
              <Grid size={12}>
                <TextField
                  size="small"
                  fullWidth
                  label="Paciente"
                  name="paciente"
                  value={formData.paciente}
                  onChange={handleChange}
                  required
                />
              </Grid>
              <Grid size={12}>
                <TextField
                  size="small"
                  fullWidth
                  label="Fecha"
                  name="fecha"
                  type="date"
                  value={formData.fecha}
                  onChange={handleChange}
                  required
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
            </Grid>
          </Grid>

          <Grid size={{ xs: 12, sm: 6 }}>
            <TextField
              size="small"
              fullWidth
              label="Motivo de la Consulta"
              name="motivoConsulta"
              value={formData.motivoConsulta}
              onChange={handleChange}
              required
              multiline
              rows={4}
            />
          </Grid>
          <Grid size={{ xs: 12 }}>
            <Divider textAlign="left">
              <FormControlLabel
                control={
                  <Checkbox
                    checked={formData.citaHoy}
                    onChange={handleChange}
                    name="citaHoy"
                    color="primary"
                  />
                }
                label="Se atendió hoy"
              />
            </Divider>
          </Grid>

          <Grid container>
            <Grid size={{ sm: 12, md: 8 }}>
              <Grid container spacing={2}>
                <Grid size={{ xs: 12, md: 6 }}>
                  <TextField
                    size="small"
                    fullWidth
                    label="Diagnóstico"
                    name="diagnostico"
                    value={formData.diagnostico}
                    disabled={!formData.citaHoy}
                    onChange={handleChange}
                    required
                  />
                </Grid>

                <Grid size={{ xs: 12, md: 6 }}>
                  <TextField
                    size="small"
                    fullWidth
                    label="¿Quién Atendió?"
                    name="quienAtendio"
                    value={formData.quienAtendio}
                    disabled={!formData.citaHoy}
                    onChange={handleChange}
                    required
                  />
                </Grid>

                <Grid size={{ xs: 12, md: 6 }}>
                  <TextField
                    size="small"
                    fullWidth
                    label="Pago Recibido"
                    name="pagoRecibido"
                    type="number"
                    value={formData.pagoRecibido}
                    disabled={!formData.citaHoy}
                    onChange={handleChange}
                    required
                  />
                </Grid>

                <Grid size={{ xs: 12, md: 6 }}>
                  <TextField
                    size="small"
                    fullWidth
                    label="Tipo de Pago"
                    name="tipoPago"
                    select
                    value={formData.tipoPago}
                    disabled={!formData.citaHoy}
                    onChange={handleChange}
                    required
                  >
                    <MenuItem value="Efectivo">Efectivo</MenuItem>
                    <MenuItem value="Tarjeta">Tarjeta</MenuItem>
                    <MenuItem value="Transferencia">Transferencia</MenuItem>
                  </TextField>
                </Grid>
              </Grid>
            </Grid>
            <Grid size={{ sm: 12, md: 4 }}>
              <TextField
                size="small"
                fullWidth
                label="Comentario Adicional"
                name="comentarioAdicional"
                multiline
                rows={4}
                value={formData.comentarioAdicional}
                disabled={!formData.citaHoy}
                onChange={handleChange}
              />
            </Grid>
          </Grid>

          <Grid size={{ xs: 12 }}>
            <Button variant="contained" color="primary" type="submit">
              Enviar
            </Button>
          </Grid>
        </Grid>
      </form>
    </div>
  );
};

export default FormularioConsulta;
